import React, {useState, useEffect} from "react";
import axios from 'axios';
import Pagination from "react-js-pagination";
import XLSX from "xlsx";
import Postcode from '@actbase/react-daum-postcode';
import FileSaver from "file-saver";
import DataListInput from "react-datalist-input";
import '../../../assets/css/table.css';
import '../../../assets/css/default.css';
import '../../../assets/css/admin_contact.css';
import GreyButton from '../../modules/button/admin_grey_btn';
import BlueButton from '../../modules/button/admin_blue_btn';
import WhiteButton from '../../modules/button/admin_white_btn';
import CommonModal from '../../modules/modal/common';

function AdminContact() {
    const [keyword, setKeyword] = useState('');
    const [contactDate, setContactDate] = useState('');
    const [contactStartDate, setContactStartDate] = useState('');
    const [contactEndDate, setContactEndDate] = useState('');
    const [contactMemo, setContactMemo] = useState('');
    const [statusSelect, setStatusSelect] = useState(-2);

    const [clickedContact, setClickedContact] = useState(null);
    const [applicationInfo, setapplicationInfo] = useState(null);
    const [isEdit, setisEdit] = useState(false);
    const [page, setPage] = useState(1);
    const postsPerPage = 8;

    const [isOpen, setisOpen] = useState(false);
    const [isAgree, setisAgree] = useState(false);
    const [addApplicationInfo, setaddApplicationInfo] = useState({zip_code:"", admin_email:"", city:"", district:"", addr1:"", addr2:"", memo:"", attendee_number:"50", attendeeTarget:[0,0,0,0,0,0,0,0]});
    const [morning, setmorning] = useState(false);
    const [afternoon, setafternoon] = useState(false);
    const [dinner, setdinner] = useState(false);
    const [lectureDates, setlectureDates] = useState(null);
    const [newChurchAdd, setnewChurchAdd] = useState(false);
    const [churchData, setchurchData] = useState([]);
    const [churchInfo, setchurchInfo] = useState(null);
    const [selectChurch, setselectChurch] = useState(null);
    const [isPostCodeOpen, setisPostCodeOpen] = useState(false);
    const [lectureData, setlectureData] = useState(null);
    const [selectedLecture, setselectedLecture] = useState(null);

    useEffect(() => {
        readApplicationForms();
        readChurchInfo();
        readLecture();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        readApplicationForms();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [statusSelect])


    useEffect(() => {
        var churchArray = [];
        if(churchInfo !== null) {
            for(var i=0 ; i<churchInfo.length ; i++) {
                churchArray.push({label:churchInfo[i].name, key:churchInfo[i].id})
            }
            setchurchData(churchArray);
        }
    }, [churchInfo])

    useEffect(() => {
        if(selectedLecture !== null)
            readLectureDate(selectedLecture.id);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedLecture])

    useEffect(() => {
        if(selectChurch !== null) {
            setaddApplicationInfo({
                ...addApplicationInfo,
                church: selectChurch.name,
                addr1: selectChurch.addr1,
                addr2: selectChurch.addr2,
                zip_code: selectChurch.zip_code,
                city: selectChurch.city,
                district: selectChurch.district,
                pastor: selectChurch.pastor,
                phone: selectChurch.phone,
                email: selectChurch.email
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectChurch])

    const readLecture = async () => {

        const response = await axios.get(
            process.env.REACT_APP_RESTAPI_HOST + 'lecture/subject', {
                params: {
                    token: window.sessionStorage.getItem('token'),
                    manageID: window.sessionStorage.getItem('id')
                }
            }
        );
        setlectureData(response.data);
        setselectedLecture(response.data[0]);
        setaddApplicationInfo({
            ...addApplicationInfo,
            lecture_id: response.data[0].id
        });
    }

    const readChurchInfo = async () => {
        const response = await axios.get(
            process.env.REACT_APP_RESTAPI_HOST + 'church', {
                params: {
                    token: window.sessionStorage.getItem('token'),
                    manageID: window.sessionStorage.getItem('id')
                }
            }
        );
        setchurchInfo(response.data);
    }

    const searchKeywordChanged = e => {
        setKeyword(e.target.value);
    }

    const readApplicationForms = async() => {
        const response = await axios.get(
            process.env.REACT_APP_RESTAPI_HOST + 'application',
            {
                params: {
                    keyword: keyword,
                    statusSelect: statusSelect,
                    token: window.sessionStorage.getItem('token'),
                    manageID: window.sessionStorage.getItem('id')
                }
            }
        );
        setapplicationInfo(response.data);
        setKeyword('');
    }

    const cancelContact = async() => {
        if(window.confirm(" ?")) {
            axios(
                {
                    url: process.env.REACT_APP_RESTAPI_HOST+'application/status',
                    method: 'put',
                    data: {
                        application_form_id: clickedContact.id,
                        status: -1,
                        token: window.sessionStorage.getItem('token'),
                        manageID: window.sessionStorage.getItem('id')
                    }
                }
            ).then(function(res) {
                readApplicationForms();
                setClickedContact({
                    ...clickedContact,
                    status: -1
                })
                alert(" .")
            })
        }
    }

    const contactStart = async() => {
        if(window.confirm("  ?")) {
            axios(
                {
                    url: process.env.REACT_APP_RESTAPI_HOST+'application/status',
                    method: 'put',
                    data: {
                        application_form_id: clickedContact.id,
                        status: 1,
                        token: window.sessionStorage.getItem('token'),
                        manageID: window.sessionStorage.getItem('id')
                    }
                }
            ).then(function(res) {
                readApplicationForms();
                setClickedContact({
                    ...clickedContact,
                    status: 1
                })
            })
        }
    }

    const finishContact = async() => {
        if(window.confirm(" ?")) {
            axios(
                {
                    url: process.env.REACT_APP_RESTAPI_HOST+'application/complete',
                    method: 'put',
                    data: {
                        selectDate: contactDate,
                        contact_start_date: contactStartDate,
                        contact_end_date: contactEndDate,
                        application_form_id: clickedContact.id,
                        lecture_id: clickedContact.lecture_id,
                        contact_memo: contactMemo,
                        status:2,
                        token: window.sessionStorage.getItem('token'),
                        manageID: window.sessionStorage.getItem('id')
                    }
                }
            ).then(function(res) {
                readApplicationForms();
                alert(" .")
                setClickedContact(null);
                setisEdit(false);
            })
        }
    }

    function clickContact(application) {
        setisEdit(false)
        if(clickedContact === null || application !== clickedContact) {
            setClickedContact(application);
            if(application.date === null)   setContactDate("")
            else setContactDate(application.date)
            if(application.contact_start_date === null)   setContactStartDate("")
            else setContactStartDate(application.contact_start_date)
            if(application.contact_end_date === null)   setContactEndDate("")
            else setContactEndDate(application.contact_end_date)
            if(application.memo === null)   setContactMemo("")
            else setContactMemo(application.memo)
        }
        else
            setClickedContact(null);
    }

    const clickEditBtn = () => {
        setisEdit(true)
    }

    const clickBackBtn = () => {
        setisEdit(false);
        if(clickedContact.date === null)   setContactDate("")
        else setContactDate(clickedContact.date)
        if(clickedContact.contact_start_date === null)   setContactStartDate("")
        else setContactStartDate(clickedContact.contact_start_date)
        if(clickedContact.contact_end_date === null)   setContactEndDate("")
        else setContactEndDate(clickedContact.contact_end_date)
        if(clickedContact.memo === null)   setContactMemo("")
        else setContactMemo(clickedContact.memo)
    }

    const handlePageChange = (page) => { 
        setPage(page); 
    };

    function currentPosts(tmp) {
        var indexOfLast = page * postsPerPage;
        var indexOfFirst = indexOfLast - postsPerPage;

        let currentPosts = 0;
        currentPosts = applicationInfo.slice(indexOfFirst, indexOfLast);
        return currentPosts;
    }

    const exportToCSV = () => {
        const fileType = "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
        const fileExtension = ".xlsx";
        const fileName = "contact";
        const ws = XLSX.utils.json_to_sheet(applicationInfo);
        const wb = { Sheets: { data: ws }, SheetNames: ["data"] };
        const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" });
        const data = new Blob([excelBuffer], { type: fileType });
        FileSaver.saveAs(data, fileName + fileExtension);
    };

    const closeModal = () => {
        setaddApplicationInfo({zip_code:"", city:"", district:"", admin_email:"", addr1:"", addr2:"", memo:"", phone:"", email:"", pastor:"", lecture_id: lectureData[0].id, lecture_date_id:0, attendee_number:"50", attendee_target:"", attendeeTarget:[0,0,0,0,0,0,0,0]});
        setmorning(false);
        setafternoon(false);
        setdinner(false);
        setselectedLecture(lectureData[0]);
        setselectChurch(null);
        setnewChurchAdd(false);
        setisOpen(false);
    }

    const onAdd = (e) => {
        const { value, name } = e.target;

        setaddApplicationInfo({
            ...addApplicationInfo,
            [name]: value
        });
    };

    const setMorning = () => {
        if(morning)
            setmorning(false)
        else
            setmorning(true)
    }
    const setAfternoon = () => {
        if(afternoon)
            setafternoon(false)
        else
            setafternoon(true)
    }
    const setDinner = () => {
        if(dinner)
            setdinner(false)
        else
            setdinner(true)
    }

    const setIsAgree = () => {
        if(isAgree)
            setisAgree(false)
        else
            setisAgree(true)
    }

    const readLectureDate = async (lecture_id) => {
        const response = await axios.get(
            process.env.REACT_APP_RESTAPI_HOST + 'lectureDate', {
                params: {
                    lecture_id: lecture_id,
                    token: window.sessionStorage.getItem('token'),
                    manageID: window.sessionStorage.getItem('id')
                }
            }
        );
        if(response.data !== null && response.data.length > 0) 
        setaddApplicationInfo({
            ...addApplicationInfo,
            lecture_id: lecture_id,
            lecture_date_id: response.data[0].id
        });
        else
        setaddApplicationInfo({
            ...addApplicationInfo,
            lecture_id: lecture_id,
            lecture_date_id: 0
        });
        setlectureDates(response.data);
    }

    const handleChange = (selectedItem) => {
        setselectChurch(null);
        setaddApplicationInfo({
            ...addApplicationInfo,
            church_id: selectedItem.key,
        });
        setselectChurch(churchInfo.find(element => element.id === selectedItem.key))
    };

    const clickAddBtn = () => {
        setselectChurch(null);

        if(newChurchAdd) {
            setnewChurchAdd(false)
            setaddApplicationInfo({
                ...addApplicationInfo,
                zip_code:"", 
                addr1:"", 
                addr2:"",
                city:"", 
                district:"",
                pastor:"",
                email:"",
                phone: ""
            })
        }
        else {
            setnewChurchAdd(true);
            setaddApplicationInfo({
                ...addApplicationInfo,
                zip_code:"", 
                addr1:"", 
                addr2:"", 
                city:"", 
                district:"",
                pastor:"",
                email:"",
                phone: "",
                church_id: 0
            })
        }
    }

    const selectPostCode = (data) => {
        setaddApplicationInfo({
            ...addApplicationInfo,
            addr1: data.address,
            city: data.sido,
            district: data.sigungu,
            zip_code: data.zonecode
        });
        setisPostCodeOpen(false);
    }

    const lectureChange = (e) => {
        
        // var data = lectureData.find(element => element.id == e.target.value);
        var data = null;
        for(var i=0 ; i<lectureData.length ; i++) {
            if(lectureData[i].id == e.target.value) {
                console.log("");
                data = lectureData[i];
            }
        }
        console.log(e.target.value, lectureData);
        console.log("data!!!!!!!",data);
        if(data.date === "") {
            setaddApplicationInfo({
                ...addApplicationInfo,
                lecture_id: e.target.value,
                lecture_date_id: 0
            })
        } else {
            setaddApplicationInfo({
                ...addApplicationInfo,
                lecture_id: e.target.value
            })
        }
        setselectedLecture(data);
    }

    const addApplication = async () => {

        var timezone = "";
        if(morning)
            timezone += " ";
        if(afternoon)
            timezone += " ";
        if(dinner)
            timezone += " ";

        if(timezone === "" || addApplicationInfo.addr1 === "" || addApplicationInfo.addr2 === "" || addApplicationInfo.admin_name === "" || addApplicationInfo.admin_phone === "" || addApplicationInfo.attendee_number === "" || addApplicationInfo.attendee_target === "" || addApplicationInfo.church_id === "" || addApplicationInfo.lecture_date_id === "" || addApplicationInfo.zip_code === "" || addApplicationInfo.lecture_id === "") {
            alert("   !")
            return;
        }

        if(newChurchAdd) {
            if(addApplicationInfo.pastor === "" || addApplicationInfo.email === "" || addApplicationInfo.phone === "") {
                alert("     !");
                return;
            }
        }

        var attendee_target = [];
        if(addApplicationInfo.attendeeTarget[0]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[1]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[2]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[3]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[4]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[5]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[6]===1) attendee_target.push("");
        if(addApplicationInfo.attendeeTarget[7]===1) attendee_target.push("");
        attendee_target = attendee_target.join(',');

        var date = "";
        if(addApplicationInfo.lecture_date_id !== 0)
            date = lectureDates.find(element => parseInt(element.id) === parseInt(applicationInfo.lecture_date_id)).date;
        
        if(!isAgree) {
            alert("  !");
            return ;
        }
        console.log(selectedLecture);
        console.log(addApplicationInfo);
        console.log(lectureDates);

        var params = new URLSearchParams();
        params.append('user_id', window.sessionStorage.getItem('id'));
        params.append('church_id', addApplicationInfo.church_id);
        params.append('zip_code', addApplicationInfo.zip_code);
        params.append('addr1', addApplicationInfo.addr1);
        params.append('addr2', addApplicationInfo.addr2);
        params.append('pastor', addApplicationInfo.pastor);
        params.append('email', addApplicationInfo.email);
        params.append('phone', addApplicationInfo.phone);
        params.append('lecture_id', addApplicationInfo.lecture_id);
        params.append('lecture_date_id', addApplicationInfo.lecture_date_id);
        params.append('memo', addApplicationInfo.memo);
        params.append('admin_phone', addApplicationInfo.admin_phone);
        params.append('admin_name', addApplicationInfo.admin_name);
        params.append('admin_email', addApplicationInfo.admin_email);
        params.append('attendee_number', addApplicationInfo.attendee_number);
        params.append('attendee_target', attendee_target);
        params.append('timezone', timezone);
        params.append('inst_email', addApplicationInfo.email);
        params.append('date', date);
        params.append('lecture_name', selectedLecture.name);
        params.append('church_name', addApplicationInfo.church);
        params.append('link', process.env.REACT_APP_HOMEPAGE);
        params.append('church', addApplicationInfo.church);
        params.append('city', addApplicationInfo.city);
        params.append('district', addApplicationInfo.district);
        params.append('token', window.sessionStorage.getItem('token'));
        params.append('manageID', window.sessionStorage.getItem('id'));

        if(window.confirm(" ?")) {
            const response = await axios.post(
            process.env.REACT_APP_RESTAPI_HOST + 'application',
            params,
            ).then(function(res) {
                alert(" .");
                readApplicationForms();
                setisOpen(false);
                setisAgree(false);
                setmorning(false);
                setafternoon(false);
                setdinner(false);
                setnewChurchAdd(false);
                setselectChurch(null);
                setaddApplicationInfo({zip_code:"", city:"", district:"", admin_email:"", addr1:"", addr2:"", memo:"", phone:"", email:"", pastor:"", lecture_id: lectureData[0].id, lecture_date_id:0, attendee_number:"50", attendee_target:"", attendeeTarget:[0,0,0,0,0,0,0,0]});
                setselectedLecture(lectureData[0]);  
            });
        }
    }

    const clickTarget = (index) => {
        var tempTarget = addApplicationInfo.attendeeTarget;
        if(tempTarget[index] === 0){
            tempTarget[index] = 1;
        } else {
            tempTarget[index] = 0;
        }            
        setaddApplicationInfo({
            ...addApplicationInfo,
            attendeeTarget: tempTarget
        })
    }

    return (
        <div>
            <p className="admin-title-header">  </p>
            <div id="contact-wrapper" className="admin-content-wrapper">
                <div>
                    <div className="table-wrapper mt0">
                        <p className="adcontact-table-name">
                              
                            <GreyButton class="right" name=" " click={() => {setisOpen(true)}}/>
                            <GreyButton class="right mr15" name="  " click={exportToCSV}/>
                        </p>
                        <CommonModal open={isOpen} close={closeModal} func={addApplication} header="  " footer=""> 
                            <div>
                                <div className="form-wrapper">
                                    <div className="mb15">
                                        <span className="form-title">  </span>
                                        <select defaultValue={addApplicationInfo.lecture_id} name="lecture_id" className="p48" onChange={lectureChange}>
                                            {lectureData !== null &&
                                            lectureData.map((lectureData, index) =>
                                                <option key={lectureData.id} value={lectureData.id}>{lectureData.name}</option>
                                            )
                                            }
                                        </select>
                                    </div>
                                    {/* <div className="mb15">
                                        <span className="form-title"></span>
                                        <span>{selectedLecture !== null ? selectedLecture.name : " "}</span>
                                    </div> */}
                                    <div className="mb15">
                                        <span className="form-title"></span>
                                        <span>{ selectedLecture !== null ? selectedLecture.instructor_name : " "}</span>
                                    </div>
                                    <hr className="mb30"/>
                                    <div className="mb15">
                                        <span className="form-title"></span>
                                        {newChurchAdd ?
                                        <input className="form-input" type="text" placeholder=" " name="church" onChange={onAdd}/>
                                        :
                                        churchData !== null ?
                                        <DataListInput
                                        style={{width:"100px"}}
                                        placeholder=" ."
                                        items={churchData}
                                        onSelect={handleChange}
                                        />
                                        :
                                        null
                                        }
                                        {newChurchAdd ?
                                        <BlueButton class="blue-btn" name="  " click={clickAddBtn}/>
                                        :
                                        <BlueButton class="blue-btn" name="  " click={clickAddBtn}/>
                                        }

                                    </div>
                                    {/* <CommonModal open={isModal} close={closeModal} header=" " footer=""> 
                                        <Postcode
                                            style={{ width: 320, height: 320 }}
                                            jsOptions={{ animated: true, hideMapBtn: true }}
                                            onSelected={data => {
                                                selectPostCode(data);
                                                setModal(false);
                                            }}
                                        />
                                    </CommonModal> */}

                                    
                                    <div className="mb15" style={{position: 'relative'}}>
                                        <span className="form-title"> </span>
                                        <input readOnly placeholder="" className="form-addr mr10" value={addApplicationInfo.zip_code} name="zip_code" onChange={onAdd}/> 
                                        {isPostCodeOpen ? 
                                        <BlueButton click={() => setisPostCodeOpen(false)} name="" class="blue-btn"/>:
                                        <BlueButton click={() => setisPostCodeOpen(true)} name="" class="blue-btn"/>
                                        }
                                        {isPostCodeOpen &&
                                        <Postcode
                                            style={{ width: 320, height: 320, position:'absolute', left:'170px' }}
                                            jsOptions={{ animated: true }}
                                            onSelected={data => selectPostCode(data)}
                                        />
                                        }
                                    </div>
                                    <div className="mb15">
                                        <span className="form-title"></span>
                                        <input readOnly placeholder="" className="form-addr" value={addApplicationInfo.addr1} name="addr1" onChange={onAdd}/>
                                    </div>
                                    <div className="mb35">
                                        <span className="form-title"></span>
                                        <input className="form-addr" name="addr2" onChange={onAdd} placeholder="  " value={addApplicationInfo.addr2}/>
                                    </div>
                                    {newChurchAdd &&
                                    <span>
                                        <div className="mb15">
                                            <span className="form-title"> </span>
                                            <input className="form-input" name="pastor" placeholder="  " value={addApplicationInfo.pastor} onChange={onAdd}/>
                                        </div>
                                        <div className="mb15">
                                            <span className="form-title"> </span>
                                            <input className="form-input" name="phone" placeholder="  " value={addApplicationInfo.phone} onChange={onAdd}/>
                                        </div>
                                        <div className="mb30">
                                            <span className="form-title"> </span>
                                            <input className="form-input" name="email" placeholder="  " value={addApplicationInfo.email} onChange={onAdd}/>
                                        </div>
                                    </span>
                                    }
                                    <hr className="m30 mb30"/>
                                    <div className="mb15">
                                        <span className="form-title"> </span>
                                        <input className="form-input" name="admin_name" onChange={onAdd}/>
                                    </div>
                                    <div className="mb15">
                                        <span className="form-title"> </span>
                                        <input className="form-input" name="admin_phone" onChange={onAdd}/>
                                    </div>
                                    <div className="mb30">
                                        <span className="form-title"> ()</span>
                                        <input className="form-input" name="admin_email" onChange={onAdd}/>
                                    </div>
                                    {/* <div className="mb30">
                                        <span className="form-title"> </span>
                                        <input className="form-input" name="email" onChange={onAdd}/>
                                    </div> */}
                                    <hr className="m30"/>
                                    <div className="select-wrapper">
                                        <div style={{gridColumn:'1 / span 2'}}>
                                            <span className="form-title"> </span>
                                            {addApplicationInfo !== null &&
                                                <span>
                                                    <span className={addApplicationInfo.attendeeTarget[0]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(0)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[1]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(1)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[2]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(2)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[3]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(3)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[4]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(4)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[5]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(5)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[6]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(6)}></span>
                                                    <span className={addApplicationInfo.attendeeTarget[7]===1 ? "category-btn category-active" : "category-btn"} onClick={() => clickTarget(7)}></span>
                                                </span>
                                            }
                                        </div>
                                        <div>
                                            <span className="form-title"> </span>
                                            <select className="form-input" name="attendee_number" defaultValue={addApplicationInfo.attendee_number} onChange={onAdd}>
                                                <option>50</option>
                                                <option>100</option>
                                                <option>200</option>
                                                <option>500</option>
                                                <option>1000</option>
                                                <option>1000</option>
                                            </select>
                                        </div>
                                        <div>
                                            <span className="form-title"> </span>
                                            <select className="form-input" name="lecture_date_id" defaultValue={addApplicationInfo.lecture_date_id} onChange={onAdd}>
                                            {lectureDates !== null && lectureDates.length > 0 ?
                                            lectureDates.map((date, i) => <option key={i} value={date.id}>{date.date}</option>)
                                            : <option value="0"> </option>
                                            }
                                            </select>
                                        </div>
                                        <div style={{display:"flex"}}>
                                            <span className="form-title"> </span>
                                            <WhiteButton class={morning ? "application-time application-time-select" : "application-time"} name="" click={setMorning}/>
                                            <WhiteButton class={afternoon ? "application-time application-time-select" : "application-time"} name="" click={setAfternoon}/>
                                            <WhiteButton class={dinner ? "application-time application-time-select" : "application-time"} name="" click={setDinner}/>
                                        </div>

                                    </div>

                                    <textarea className="form-textarea" placeholder="  /   " name="memo" onChange={onAdd}></textarea>

                                    <ul className="agree-ul mb40">
                                        <li className="mb10">              (  158), (  319 2), ( 314),      .</li>
                                        <li>    -        ,      .</li>
                                    </ul>
                                    <div className="mb40">
                                        <input className="agree-check mr10" type="checkbox" onClick={setIsAgree}/>
                                        <span className="fs14">     .</span>
                                    </div>
                                </div>
                            </div>
                        </CommonModal>

                        <div className="mb20">
                            <select defaultValue = {statusSelect} className="inst-group mr15 p48" onChange={(e) => {setStatusSelect(e.target.value)}}>
                                <option value="-2"></option>
                                <option value="0">()</option>
                                <option value="1"></option>
                                <option value="2"></option>
                                <option value="-1"></option>
                            </select>
                            <input className="p48 search-lecture-input" type="text" placeholder=", , , ,     " value={keyword || ""} onChange={searchKeywordChanged}/>
                            <GreyButton name="" click={readApplicationForms}/>
                        </div>
                        <div className="table-row-adcontact">
                            <span className="th"></span>
                            <span className="th"></span>
                            <span className="th"></span>
                            <span className="th"> </span>
                            <span className="th"> </span>
                        </div>
                        { applicationInfo !== null && applicationInfo.length > 0 ?
                            currentPosts(applicationInfo).map((data, index) => 
                                <div key={data.id} className={clickedContact !== null && clickedContact.id === data.id ? "click-inst-row table-row-adcontact" : "table-row-adcontact"} onClick = {() => {clickContact(data)}}>
                                    <span className="td">{data.inst_name}</span>
                                    <span className="td">{data.lecture_name}</span>
                                    <span className="td">{data.church_name}</span>
                                    <span className="td">{data.date}({data.timezone})</span>
                                    {data.status === 0 ?
                                        <span className="td"><span className="contact-ing" style={{color:'#ff0000b8', border:'1px solid #ff0000b8'}}>()</span></span>
                                        : data.status === 1 ?
                                        <span className="td"><span className="contact-ing"></span></span>
                                        : data.isVisiting > 0 ?
                                        <div className="td contact-ok"></div>
                                        : data.status === 2 ?
                                        <div className="td contact-ok"></div>:
                                        <div className="td contact-cancel"></div>
                                    }
                                </div>
                            )
                            :
                            <div className="no-content"> .</div>
                        }
                    </div>
                    <Pagination 
                        activePage={page} 
                        itemsCountPerPage={postsPerPage} 
                        totalItemsCount={applicationInfo !== null ? applicationInfo.length : 0} 
                        pageRangeDisplayed={5} 
                        prevPageText={""} 
                        nextPageText={""} 
                        onChange={handlePageChange} 
                    />
                </div>
                <div className="relative">
                {clickedContact ? 
                isEdit ?
                <div
                    className="show-lecture-detail"
                >
                    <h2 className="mb20">{clickedContact.lecture_name}</h2>
                    <hr className="bold-hr mb25"/>
                    <div className="mb8">
                        <span className="form-title"></span>
                        <span>{clickedContact.admin_name}</span>
                    </div>
                    <div>
                        <span className="form-title"></span>
                        <span>{clickedContact.admin_phone}</span>
                    </div>
                    <hr className="m20"/>
                    <div className="mb8">
                        <span className="form-title"></span>
                        <span>{clickedContact.church_name}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"></span>
                        <span>{clickedContact.addr1} {clickedContact.addr2}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.phone}</span>
                    </div>
                    <div>
                        <span className="form-title"> </span>
                        <span>{clickedContact.email}</span>
                    </div>
                    <hr className="m20"/>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.attendee_target}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.attendee_number}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <input className="form-input" type="date" value={contactDate} onChange={(e)=>setContactDate(e.target.value)}/>
                    </div>
                    <div className="mb35">
                        <span className="form-title"> </span>
                        <input className="form-input" type="time" value={contactStartDate} onChange={(e)=>setContactStartDate(e.target.value)}/> ~ <input className="form-input" type="time" value={contactEndDate} onChange={(e)=>setContactEndDate(e.target.value)}/>
                    </div>
                    <div className="mb25 form-grid">
                        <span className="form-title"></span>
                        <textarea className="form-textarea" type="date" value={contactMemo.replaceAll("<br/>","\n")} onChange={(e)=>setContactMemo(e.target.value)}/>
                    </div>
                    <hr className="bold-hr mb35"/>
                    <div className="form-btn-wrapper">
                        <button className="form-btn mr15" onClick={finishContact}>  </button>
                        <button className="form-btn" onClick={clickBackBtn}></button>
                    </div>
                </div>
                :
                <div
                    className="show-lecture-detail"
                >
                    <h2 className="mb20">{clickedContact.lecture_name}</h2>
                    <hr className="bold-hr mb25"/>
                    {clickedContact.status !== 0 && 
                    <span>
                        <div className="mb8">
                            <span className="form-title"></span>
                            <span>{clickedContact.admin_name}</span>
                        </div>
                        <div>
                            <span className="form-title"></span>
                            <span>{clickedContact.admin_phone}</span>
                        </div>
                        <hr className="m20"/>
                    </span>
                    }
                    <div className="mb8">
                        <span className="form-title"></span>
                        <span>{clickedContact.church_name}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"></span>
                        <span>{clickedContact.addr1} {clickedContact.addr2}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.phone}</span>
                    </div>
                    <div>
                        <span className="form-title"> </span>
                        <span>{clickedContact.email}</span>
                    </div>
                    <hr className="m20"/>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.attendee_target}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.attendee_number}</span>
                    </div>
                    <div className="mb8">
                        <span className="form-title"> </span>
                        <span>{clickedContact.date}</span>
                    </div>
                    <div className="mb35">
                        <span className="form-title"> </span>
                        {clickedContact.status === 2 ?
                        <span>{clickedContact.contact_start_date} ~ {clickedContact.contact_end_date}</span>:
                        <span>{clickedContact.timezone}</span>}
                    </div>
                    <div className="mb25 form-grid">
                        <span className="form-title"></span>
                        <span>{clickedContact.memo.split("<br/>").map( (item, i) => <div key={i}>{item}</div>)}</span>
                    </div>
                    <hr className="bold-hr mb35"/>
                    {clickedContact.status === 0 ?
                    <div className="form-btn-wrapper">
                        <button className="form-btn mr15" onClick={contactStart}> </button>
                        {/* <button className="form-btn" onClick={cancelContact}> </button> */}
                    </div>
                    : clickedContact.status === 1 ?
                    <div className="form-btn-wrapper">
                        <button className="form-btn mr15" onClick={clickEditBtn}> </button>
                        <button className="form-btn" onClick={cancelContact}> </button>
                    </div>
                    : clickedContact.status === 2 ?
                    <div className="form-btn-wrapper">
                        <button className="form-btn" onClick={clickEditBtn}> </button>
                        <button className="form-btn" onClick={cancelContact}> </button>
                    </div>
                    :null
                    }
                </div>
                :
                <div className="select-inst">
                      .
                </div>
                }
                </div>
            </div>
        </div>
    )
}

export default AdminContact;