const CommonModal = ( props ) => {
    // , ,     
    const { open, close, func, header, footer } = props;

    return (
        //   openModal  .
        <div className={ open ? 'openModal modal' : 'modal' }>
            { open ? (  
                <section>
                    <header>
                        {header}
                        <button className="close" onClick={close}> &times; </button>
                    </header>
                    <main>
                        {props.children}
                    </main>
                    <footer>
                        <button className="close" onClick={func}> {footer} </button>
                    </footer>
                </section>
            ) : null }
        </div>
    )
}

export default CommonModal;